* {
    margin: 0;
    border: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(247, 247, 247);
}

input {
    min-width: 300px;
    background-color: inherit;
}

.margin-left-auto {
    margin-left: auto;
}